import { useState, useEffect } from "react";
import { Popup } from "./Popup";

import {
  useAccount,
  useBalance,
  useContractRead,
  useContractWrite,
  usePrepareContractWrite,
  useWaitForTransaction,
} from "wagmi";
import contractInterface from "./contract_abi.json";
import { ethers } from "ethers";

const cAddress = "0xe156e38674bA145B8C05157E692c3A0CFE6e83c8";

const contractConfig = {
  address: cAddress,
  abi: contractInterface,
};

const TotalSupply = () => {
  const [totalMinted, setTotalMinted] = useState(0);

  const { data: totalSupplyData } = useContractRead({
    ...contractConfig,
    functionName: "totalSupply",
    watch: true,
  });

  useEffect(() => {
    if (totalSupplyData) {
      setTotalMinted(totalSupplyData.toNumber());
    }
  }, [totalSupplyData]);

  return <h2>{totalMinted}/7470</h2>;
};

export const FreeMint = () => {
    
    const { config: contractWriteConfig, status: prepareStatus } =
    usePrepareContractWrite({
      ...contractConfig,
      functionName: "freeMint",
     
    });

    const {
        data: mintData,
        writeAsync: mint,
        isLoading: isMintLoading,
        isSuccess: isMintStarted,
        error: mintError,
      } = useContractWrite(contractWriteConfig);
      

      const onMintClick = async ()=>{
        try {
            mint?.()
        } catch (error) {
            
        }
      }

      const {
        data: txData,
        isSuccess: txSuccess,
        error: txError,
      } = useWaitForTransaction({ hash: mintData?.hash });
    
      const isMinted = txSuccess;
    

  return (
    <>
       
      {isMintLoading && <h2>"Waiting for approval"</h2>}
      {!isMinted && isMintStarted && <h2> "Free Minting..." </h2>}
      {isMinted && (
        <h2>
          {" "}
          <a href={`https://polygonscan.com/tx/${mintData?.hash}`}>
            {" "}
            Check Polygonscan{" "}
          </a>{" "}
        </h2>
      )}

        <a
          className="mint-button"
          onClick={onMintClick}
        >
          <h2> Free Mint</h2>
        </a>
       
    </>
  );
};

export const PaidMint = () => {
  const price = 1;

  const [mintAmount, setMintAmount] = useState(1);

  const { config: contractWriteConfig, status: prepareStatus } =
    usePrepareContractWrite({
      ...contractConfig,
      functionName: "mint",
      args: [
        `${mintAmount}`,
        {
          value: ethers.utils.parseEther(`${price * parseInt(mintAmount)}`),
        },
      ],
    });

  const {
    data: mintData,
    writeAsync: mint,
    isLoading: isMintLoading,
    isSuccess: isMintStarted,
    error: mintError,
  } = useContractWrite(contractWriteConfig);

  const updateValue = (e) => {
    setMintAmount(e.target.value);
  };
  const onMintClick = async () => {
    console.log(mintAmount, "onclick");

    try {
      await mint?.();
    } catch (error) {}
  };
  const {
    data: txData,
    isSuccess: txSuccess,
    error: txError,
  } = useWaitForTransaction({ hash: mintData?.hash });

  const isMinted = txSuccess;

  return (
    <>
      {" "}
      {isMintLoading && <h2>"Waiting for approval"</h2>}
      {!isMinted && isMintStarted && <h2> "Minting..." </h2>}
      {isMinted && (
        <h2>
          {" "}
          <a href={`https://polygonscan.com/tx/${mintData?.hash}`}>
            {" "}
            Check Polygonscan{" "}
          </a>{" "}
        </h2>
      )}
      {!0 && (
        <div>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <select
              value={mintAmount}
              onChange={updateValue}
              className="mint-amount"
            >
              <option>1</option>
              <option>2</option>
              <option>3</option>
              <option>4</option>
              <option>5</option>
              <option>6</option>
            </select>
            <a className="mint-button paid" onClick={onMintClick}>
              <h2>Paid Mint</h2>
            </a>
          </div>
        </div>
      )}
    </>
  );
};

export const Mint = () => {
 
  return (
    <>
      <div
        style={{
          display: "flex",
          justifyContent: "space-evenly",
          flexDirection: "row",
          margin: "4px",
        }}
      >
        <TotalSupply />
        <PaidMint />
        <FreeMint/>
      </div>
    </>
  );
};
